@import "/src/style/variables.scss";
@import "/src/style/mixins.scss";

.wrapper {
  grid-column: 2;

  .nav_list {
    display: flex;
    justify-content: center;

    li {
      list-style: none;

      button{
        @include blue-btn;
      }
    }

    .nav_list_item {
      margin-right: 30px;
      padding: 0;
      cursor: pointer;
    }

    .flow_name{
      position: absolute;
      left: 110px;
      font-style: italic;
      font-weight: 400;
      color: $color-list-label;
      bottom: 11px;

    }

    .server_button {
      position: absolute;
      left: 20px;
      top: 6px;

      button {
        @include blue-btn;

        &:hover {
          background-color: $bg-top-menu-btn-hover;
        }

        transition: background-color 0.2s ease-in-out;
      }
    }
  }

  .view_section_hidden {
    display: none;
  }
}
