@import "/src/style/variables.scss";
@import "/src/style/mixins.scss";


.wrapper {
    background-color: $bg-node-nav-container;
    z-index: 1000;
    padding: 0;
    height: 40px;
    border-bottom: 1px solid rgb(241, 241, 241);
    position: fixed;
    display: grid;
    grid-template-columns: 4fr 4fr 4fr;
    align-items: center;
    align-content: center;
    top: 0;
    left: 0;
    right: 0;

    .settings_wrapper {
        display: flex;
        align-items: center;
        justify-self: end;
        justify-content: space-between;
        
        align-self: flex-end; /* Use align-self to align the container to the end */
    }
    
    @media print {
        display: none;
    }

}